.emails-editor_form {
  background: #FFFFFF;
  border: 1px solid #C3C2CF;
  border-radius: 4px;
  height: 96px;
  overflow-y: auto;
  padding: 8px 7px;
  cursor: text;
  overflow-wrap: break-word; }

.emails-editor_input {
  min-width: 2px;
  border: none;
  margin-right: 8px; }
  .emails-editor_input:focus {
    outline: none !important; }

.emails-editor_placeholder {
  color: #C3C2CF;
  font-size: 14px;
  line-height: 24px; }

.emails-editor_email {
  font-size: 14px;
  margin-right: 8px;
  margin-bottom: 4px;
  display: inline-block; }
  .emails-editor_email__valid {
    background: rgba(102, 153, 255, 0.2);
    border-radius: 100px;
    padding: 5px 10px; }
  .emails-editor_email__invalid {
    border-bottom: 1px dashed #D92929;
    padding: 1px 0; }
